<template>
  <div @click="fnShowVoucherType" @touchmove.prevent class="voucher-type">
    <div @click.stop class="content">
      <div class="tit">
        <div class="left">请选择类别</div>
        <img @click="fnShowVoucherType" alt class="right" :src="`${$cdnImageUrl}/shop/icon-arrow2.png`" />
      </div>
      <ul>
        <li
          :class="{active:item.id===couponCateId}"
          :key="index"
          @click="fnChange(item)"
          v-for="(item,index) in couponCateList"
        >{{item.cateName}}</li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  props: ['couponCateList', 'couponCateId'],
  data() {
    return {}
  },
  mounted() {},
  methods: {
    fnChange(item) {
      this.$emit('fnChange', item)
      this.fnShowVoucherType()
    },
    fnShowVoucherType() {
      this.$emit('fnShowVoucherType')
    },
  },
}
</script>

